    //   console.log(res);
    //   var that = this;
    //   that.attr.productSelect.cart_num = 1;
    //   that.$dialog.message("每人每次限购1" + that.storeInfo.unit_name);
    // },
    //购物车；
    ChangeCartNum: function ChangeCartNum(res) {
      //changeValue:是否 加|减
      //获取当前变动属性
      var productSelect = this.productValue[this.attrValue]; //如果没有属性,赋值给商品默认库存

      if (productSelect === undefined && !this.attr.productAttr.length) productSelect = this.attr.productSelect;
      if (productSelect === undefined) return;
      var quota = productSelect.quota || 0;
      var num = this.attr.productSelect;

      if (res) {
        num.cart_num++;

        if (num.cart_num > quota) {
          this.$set(this.attr.productSelect, "cart_num", quota);
          this.$set(this, "cart_num", quota);
        }
      } else {
        num.cart_num--;

        if (num.cart_num < 1) {
          this.$set(this.attr.productSelect, "cart_num", 1);
          this.$set(this, "cart_num", 1);
        }
      }
    },
    setProductSelect: function setProductSelect() {
      var that = this;
      var attr = that.attr;
      attr.productSelect.image = that.storeInfo.image;
      attr.productSelect.store_name = that.storeInfo.title;
      attr.productSelect.price = that.storeInfo.price;
      attr.productSelect.quota = 0;
      attr.productSelect.quota_show = 0;
      attr.productSelect.product_stock = 0;
      attr.cartAttr = false;
      that.$set(that, "attr", attr);
    },
    openTeam: function openTeam() {
      var _this = this;

      var that = this;

      if (that.attr.cartAttr == false) {
        that.attr.cartAttr = !this.attr.cartAttr;
      } else {
        var data = {};
        data.productId = that.storeInfo.product_id;
        data.cartNum = that.attr.productSelect.cart_num;
        data.uniqueId = that.attr.productSelect.unique;
        data.combinationId = that.storeInfo.id;
        data.new = 1;
        postCartAdd(data).then(function (res) {
          that.$router.push({
            path: "/order/submit/" + res.data.cartId
          });
        }).catch(function (res) {
          _this.$dialog.error(res.msg);
        });
      }
    },
    handleScroll: function handleScroll() {
      var top = document.body.scrollTop || document.documentElement.scrollTop;
      var opacity = top / 350;
      opacity = opacity > 1 ? 1 : opacity;
      this.opacity = opacity;
    },
    asideTap: function asideTap(a) {
      var _this2 = this;

      this.$nextTick(function () {
        var index = a;
        _this2.navActive = index;
        var element = document.querySelector("#title" + index);
        var top = element.offsetTop - _this2.$refs.header.offsetHeight - window.scrollY;
        _this2.lock = true;
        window.scrollBy({
          top: top,
          left: 0,
          behavior: "smooth"
        });
      });
    },
    onScroll: debounce(function () {
      var _this3 = this;

      if (this.lock) {
        this.lock = false;
        return;
      }

      var headerHeight = this.$refs.header.offsetHeight,
          _window = window,
          scrollY = _window.scrollY,
          titles = [];
      titles.push(document.querySelector("#title0"));
      titles.push(document.querySelector("#title1"));
      titles.push(document.querySelector("#title2"));
      titles.reduce(function (initial, title, index) {
        if (!document.querySelector("#title0") && !document.querySelector("#title1") && !document.querySelector("#title2")) {
          return;
        }

        if (initial) return initial;

        if (scrollY + headerHeight < title.offsetTop + title.offsetHeight) {
          initial = true;
          _this3.navActive = index;
        }

        return initial;
      }, false);
      this.lock = true;
    }, 500)
  },
  beforeDestroy: function beforeDestroy() {
    document.removeEventListener("scroll", this.onScroll, false);
    window.removeEventListener("scroll", this.handleScroll);
  }
};